<template>
  <div class="gradient">
    <div class="own-container">
      <div class="own-panel" v-if="list[1].items">
        <van-pull-refresh
                v-model="list[1].refreshing"
                @refresh="onRefreshHistory">
          <van-list
                  v-model="list[1].loading"
                  :finished="list[1].finished"
                  finished-text="没有更多了"
                  @load="onLoadHistory">
            <van-cell v-for="item in list[1].items" :key="item._id" @click="loadingGoArticle(item.articleOrder.id)">

              <van-row>
                <van-col span="22">
                  <div class="article-title">
                    {{item.article.article_title}}
                  </div>
                  <div class="article-content own-ellipsis">
                    {{item.article.article_brief}}
                  </div>
                  <van-row>
                    <van-col span="4">难度：</van-col>
                    <van-col span="12">
                      <van-rate v-model="item.article.article_grade + 1"/>
                    </van-col>
                  </van-row>
                </van-col>
                <van-col span="2">
                  <div class="read-box">
                    <van-icon name="arrow" size="20px"/>
                  </div>
                </van-col>
              </van-row>
            </van-cell>
          </van-list>
        </van-pull-refresh>
      </div>
      <div class="own-panel" v-else>暂无学习内容</div>
    </div>
  </div>
</template>

<script>
  import ConfirmDialog from '@/components/ConfirmDialog.vue'
  import OrderDialog from '@/components/OrderDialog.vue'
  import {NoticeBar, Panel} from 'vant';
  import {mapGetters} from "vuex";
  import Calendar from 'vue-calendar-component';

  export default {
    name: 'learned',
    components: {
      ConfirmDialog,
      OrderDialog,
      [NoticeBar.name]: NoticeBar,
      [Panel.name]: Panel,
      Calendar,
    },
    computed: {
      ...mapGetters(["userInfo"])
    },
    created() {
    },
    async mounted() {
      this.params.openId = this.userInfo.openid;
      this.orderParams.openid = this.userInfo.openid;
      this.payParams.openId = this.userInfo.openid;
      this.wxShare.share()
    },
    data() {
      return {
        learnDays: [],
        confirmOptions: {
          show: false,
          title: '还在犹豫？',
          msg: '联系督学班长了解下？',
          cancelText: '重新支付',
          confirmText: '跟她聊聊',
          cancelAction: () => {
            this.submitPay();
          },
          confirmAction: () => {
            this.$router.replace('/groupCode')
          }
        },
        orderParams: {
          article: '',  // 5cd245a1ea21fd0efceed74b
          openid: '', // oAfgA1QTrvWbYXZ4iU2sGKMLEs5g
          pay_type: 1
        },
        payParams: {
          orderId: '',
          openId: ''
        },
        cancelPayShow: false,
        showPayBox: false,
        params: {
          page: 1,
          limit: 5,
          openId: ''
        },
        active: 0,
        ownBarClass: 'own-nav-b',
        list: [{
          items: [],
          refreshing: false,
          loading: false,
          error: false,
          finished: false
        },
          {
            items: [],
            refreshing: false,
            loading: false,
            error: false,
            finished: false
          }
        ]
      }
    },
    methods: {
      clickDay(data) {
        console.log(data); //选中某天
      },
      changeDate(data) {
        console.log(data); //左右点击切换月份
      },
      clickToday(data) {
        console.log(data); // 跳到了本月
      },
      closeConfirmDialog(val) {

      },
      loadingGoArticle(orderId) {
        this.$router.push({
          name: 'payedArticle',
          params: {
            id: orderId
          }
        })
      },
      onLoadHistory() {
        // this.params.openId = 'oAfgA1QTrvWbYXZ4iU2sGKMLEs5g';
        this.apis.article.getPayedArticleList(this.params).then(res => {
          this.list[1].items = this.list[1].items.concat(res.data.docs);
          this.list[1].loading = false;
          this.params.page++;
          if (this.list[1].items.length >= res.data.total) {
            this.list[1].finished = true;
          }
        });
      },
      onRefreshHistory() {
        setTimeout(() => {
          this.list[1].items = [];
          this.list[1].error = false;
          this.list[1].finished = false;
          this.list[1].refreshing = false;
          this.params.page = 1;
          window.scrollTo(0, 10);
        }, 1000)
      }
    },

  }
</script>
<style lang="css" scoped>
  .own-container {
    padding: 10px 0;
  }

  .own-page-title {
    font-size: 4.2vw;
    font-weight: 600;
  }

  .own-panel {
    margin-top: 10px;
    margin-bottom: 60px;
    width: 100%;
    padding: 10px;
    background: #fff;
    border-radius: 4px;
  }

  .own-panel-title {
    width: 100%;
    height: 5vw;
    font-weight: 600;
    line-height: 4vw;
    text-align: center;

  }

  .article-title {
    width: 90%;
    height: 6vw;
    font-size: 4vw;
    line-height: 6vw;
    text-align: left;
    font-weight: 500;
    overflow: hidden;
    white-space: nowrap;
    text-overflow:ellipsis;
  }

  .article-content {
    width: 100%;
    height: 10vw;
    font-size: 3.2vw;
  }

  .own-ellipsis {
    width: 100%;
    height: 11.6vw;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
  }

  .read-box{
    margin-top: 9vw;
    /*margin-left: 6vw;*/
    width: 11vw;
    height: 11vw;
  }
</style>